main {
  scroll-behavior: smooth;
}

nav {
  --y-padding: 24px;
  width: 100vw;
  padding: var(--y-padding) 24px var(--y-padding) 0px;
  z-index: 3;
  flex-direction: row;
  align-items: center;
  display: flex;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
}

nav a {
  font-size: 20px;
}

#logo {
  width: 120px;
  margin: -20px 28px -10px 2em;
}

@media screen and (width <= 700px) and (orientation: portrait) {
  #logo {
    width: 90px;
    margin: -20px 28px -10px 3em;
  }

  #left-flex {
    display: none;
  }
}

#join {
  color: #000;
  background-color: #fff;
  border-radius: 24px;
  margin-right: 28px;
  padding: 10px 20px;
  text-decoration: none;
  box-shadow: 2px 2px 2px #615e5e80;
}

#anmal {
  color: #000;
  background-color: #fff;
  border-radius: 24px;
  margin-right: 28px;
  padding: 10px 24px;
  text-decoration: none;
  box-shadow: 2px 2px 2px #615e5e80;
}

#main-nav-container {
  flex-direction: row;
  display: flex;
}

#main-nav-container > * {
  margin-right: 24px;
}

#main-nav-container > :last-child {
  margin-right: 0;
}

#hamburger {
  display: none;
}

#main-nav-outer-container {
  order: 3;
}

#nav-overlay {
  display: none;
}

@media screen and (width <= 700px) and (orientation: portrait) {
  :root {
    --nav-h: 64px;
  }

  nav a {
    font-size: 16px;
  }

  #hamburger {
    --bar-height: 2px;
    display: initial;
    cursor: pointer;
    background: none;
    border: none;
    width: 28px;
    height: 20px;
    margin-right: 0;
    position: relative;
  }

  #hamburger > * {
    height: var(--bar-height);
    opacity: 1;
    background: #fff;
    width: 100%;
    transition: transform .25s, top .25s, width .25s, opacity .25s;
    display: block;
    position: absolute;
    left: 0;
    transform: rotate(0);
  }

  #hamburger > :first-child {
    top: 0;
  }

  #hamburger > :nth-child(2) {
    top: calc(50% - var(--bar-height) / 2);
    width: 100%;
  }

  #hamburger > :nth-child(3) {
    top: calc(100% - var(--bar-height));
  }

  #hamburger.show > :first-child {
    top: calc(50% - var(--bar-height) / 2);
    transform: rotate(45deg);
  }

  #hamburger.show > :nth-child(2) {
    opacity: 0;
    width: 0%;
  }

  #hamburger.show > :nth-child(3) {
    top: calc(50% - var(--bar-height) / 2);
    transform: rotate(-45deg);
  }

  #main-nav-outer-container {
    order: initial;
    background-color: inherit;
    position: static;
  }

  #main-nav-container {
    opacity: 0;
    pointer-events: none;
    background-color: inherit;
    color: #fff;
    box-shadow: #0000 0px 4px 4x;
    flex-direction: column;
    align-items: center;
    padding-bottom: 8px;
    transition: box-shadow .5s, transform .5s, opacity .5s;
    display: flex;
    position: absolute;
    top: 100%;
    left: 0;
    right: 0;
    transform: translateY(-8px);
  }

  #main-nav-container.show {
    opacity: 1;
    pointer-events: auto;
    transform: translateY(0);
    box-shadow: 0 4px 4px #00000045;
  }

  #main-nav-container > * {
    margin-bottom: 12px;
    margin-right: 0;
  }

  #nav-overlay {
    display: initial;
    z-index: 2;
    opacity: 0;
    background-color: #0009;
    width: 0;
    height: 0;
    transition: opacity .5s;
    position: fixed;
    top: 0;
    bottom: 0;
  }

  #nav-overlay.show {
    opacity: 1;
    width: 100vw;
    height: 100vh;
  }
}
