/*
  When navigating to an anchor point, scroll smoothly
*/
main {
    scroll-behavior: smooth;
}

nav {
    position: sticky;
    top: 0;
    --y-padding: 24px;

    display: flex;
    flex-direction: row;
    width: 100vw;
    padding: var(--y-padding) 24px var(--y-padding) 0px;
    align-items: center;

    /*
      Without z-index > 2 the mobile dropdown menu fails to cover the <mark>
      tags
    */
    z-index: 3;
}

nav a {
    font-size: 20px;
}

#logo {
    width: 120px;
    margin-top: -20px;
    margin-right: 28px;
    margin-left: 2em;
    margin-bottom: -10px;
}

@media screen and (max-width: 700px) and (orientation: portrait) {
    #logo {
        width: 90px;
        margin-top: -20px;
        margin-right: 28px;
        margin-left: 3em;
        margin-bottom: -10px;
    }

    #left-flex {
        display: none;
    }
}


#join {
    margin-right: 28px;
    /* color: white; */
    color: black;
    /* background-color: transparent; */
    background-color: white;
    /* border-style: solid; */
    /* border-width: medium; */
    /* border-color: white; */
    border-radius: 24px;
    padding: 10px 20px;
    text-decoration: none;
    box-shadow: #615e5e80 2px 2px 2px;
}

#anmal {
    margin-right: 28px;
    color: black; /* TODO Use dark-gray color from design */
    background-color: white;
    border-radius: 24px;
    padding: 10px 24px;
    text-decoration: none;
    box-shadow: #615e5e80 2px 2px 2px;
}

#main-nav-container {
    display: flex;
    flex-direction: row;

    /*
      gap: 24px; gap not supported in all targeted browsers, we simulate it
      usin margin-right on the children (see below).
    */
}

#main-nav-container > * {
    margin-right: 24px;
}
#main-nav-container > *:last-child {
    margin-right: 0px;
}

#hamburger {
    display: none;
}

#main-nav-outer-container {
    order: 3;
}

#nav-overlay {
    display: none;
}

@media screen and (max-width: 700px) and (orientation: portrait) {
    :root { --nav-h: 64px; }

    nav a {
        font-size: 16px;
    }

    /*
      The hamburger icon.

      The hamburger icons has the following CSS structure
        <button>
            <div></div>
            <div></div>
            <div></div>
        </button>

      where each <div> is styled individually using :nth-child(n) to achive the
      three bars. Then the bars are rotated / changed when the hamburger icon
      has the "show" class. This together with CSS transition creates a nice
      animation.
    */

    #hamburger {
        /* How high should an individual bar be */
        --bar-height: 2px;

        display: initial;

        position: relative;
        margin-right: 0px;
        width: 28px;
        height: 20px;
        border: none;
        background: none;
        cursor: pointer;

    }

    #hamburger > * {
        display: block;
        position: absolute;
        height: var(--bar-height);
        width: 100%;
        background: white;
        opacity: 1;
        left: 0;
        transform: rotate(0deg);
        transition: transform .25s, top .25s, width 0.25s, opacity .25s;
    }

    #hamburger > *:nth-child(1) {
        top: 0px;
    }

    #hamburger > *:nth-child(2) {
        top: calc(50% - var(--bar-height) / 2);
        width: 100%;
    }

    #hamburger > *:nth-child(3) {
        top: calc(100% - var(--bar-height));
    }

    #hamburger.show > *:nth-child(1) {
        transform: rotate(45deg);
        top: calc(50% - var(--bar-height) / 2);
    }

    #hamburger.show > *:nth-child(2) {
        width: 0%;
        opacity: 0;
    }

    #hamburger.show > *:nth-child(3) {
        transform: rotate(-45deg);
        top: calc(50% - var(--bar-height) / 2);
    }

    #main-nav-outer-container {
        order: initial;
        position: static;

        /* Have the same bg-color as the <nav> tag (which is set dynamically) */
        background-color: inherit;
    }

    #main-nav-container {
        position: absolute;
        top: 100%;
        left: 0;
        right: 0;
        transform: translateY(-8px);
        opacity: 0;
        pointer-events: none;

        /* Have the same bg-color as the <nav> tag (which is set dynamically) */
        background-color: inherit;

        color: white;
        display: flex;
        flex-direction: column;
        align-items: center;
        padding-bottom: 8px;
        box-shadow: #00000000 0px 4px 4x;

        transition: box-shadow .5s, transform .5s, opacity .5s, box-shadow .5s;;
    }

    #main-nav-container.show {
        transform: translateY(0);
        opacity: 1;
        pointer-events: auto;
        box-shadow: #00000045 0px 4px 4px;
    }

    #main-nav-container > * {
        margin-right: 0px;
        margin-bottom: 12px;
    }

    /*
      This is the thing that makes the background go dark when the dropwdown
      opens.
    */
    #nav-overlay {
        display: initial;
        position: fixed;
        top: 0px;
        bottom: 0px;
        width: 0px;
        height: 0px;
        background-color: #0009;
        z-index: 2;
        opacity: 0;
        transition: opacity .5s;
    }

    #nav-overlay.show {
        width: 100vw;
        height: 100vh;
        opacity: 1;
    }
}
